header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;

  /* * */
  width: 100%;

  /* * */
  height: 50px;
  background-color: #212121;
  z-index: 3;

  /* * */
  .container {
    position: relative;
    .hamburger {
      display: none; } } }
.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  &_item {
    border-right: 2px dotted black;
    padding-right: 28px;
    font-size: 15px;
    &:last-child {
      border-right: none; } }
  &_link {
    color: $color_white;
    font-weight: 300;
    &:hover {
      text-decoration: none; } } }
.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  &_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  &_official {
    margin-top: 6px;
    opacity: 0.5;
    color: $color_white;
    font-size: 13px;
    font-weight: 100; }

  &_call {
    color: $color_white;
    font-size: 12px;
    font-weight: 300;
    margin-top: 19px;
    text-align: right; }

  &_tel {
    display: block;
    margin-top: 8px;
    color: $color_white;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right;
    line-height: 12px; }

  &_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: $color_white;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

/*1sec */

.promo {
  min-height: 800px;
  background: url('../img/1sec/uber_bg.jpg') center (center / cover) no-repeat;
  padding: 150px 0 135px 0;
  .row {
    justify-content: center; }

  &_header {
    color: $color_white;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 60px;
    line-height: 60px;
    margin: 0;
    text-align: center; }

  &_subheader {
    color: $color_white;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;
    margin: 0;
    margin-top: 22px;
    text-align: center; }

  &_descr {
    color: $color_white;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }

  &_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
    color: $color_white;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    @include btn_hover; } }

/*2 sec */

.advantages {
  padding: 20px 0 65px 0;
  &_item {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }
  &_circle {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 26px;
    width: 113px;
    height: 113px;
    background-color: $color_blue;
    border-radius: 100%; }
  &_desc {
    width: 343px; }
  &_header {
    line-height: 21px;
    color: $color_blue;
    font-size: 17px;
    font-weight: 400; }
  &_text {
    color: #202020;
    font-size: 15px;
    font-weight: 300;
    line-height: 21px; } }

.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: $color_white;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center; }


.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  line-height: 38px; }

.subtitle {
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  color: #222222;
  text-align: center;
  margin-top: 19px; }


/*3 sec */

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url(../img/3sec/mobile_bg.png) center (center/cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url(../img/3sec/sec3_card1.png) center (center/cover) no-repeat; }
    &_2 {
      background: url(../img/3sec/sec3_card2.png) center (center/cover) no-repeat; }
    &_3 {
      background: url(../img/3sec/sec3_card3.png) center (center/cover) no-repeat; }
    &_4 {
      background: url(../img/3sec/sec3_card4.png) center (center/cover) no-repeat; }
    &_5 {
      background: url(../img/3sec/sec3_card5.png) center (center/cover) no-repeat; }
    &_6 {
      background: url(../img/3sec/sec3_card6.png) center (center/cover) no-repeat; }
    &_subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-transform: uppercase;
      color: $color_white;
      text-align: center; }
    &_plus {
      margin: 0 auto;
      width: 29px;
      height: 29px;
      background: #000000;
      border-radius: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 6px;
      position: absolute;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      cursor: pointer;
      &:before {
        content: '';
        display: block;
        width: 14px;
        height: 2px;
        background-color: $color_blue;
        transition: 0.3s all; }
      &:after {
        content: '';
        display: block;
        width: 2px;
        height: 14px;
        background-color: $color_blue;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        transition: 0.3s all; }
      &:hover {
        &:before, &:after {
          background-color: $color_pink; } } } } }
/* 4 sec */

.black {
  color: $color_white; }
.opportunities {
  padding: 57px 0 138px;
  min-height: 898px;
  background: url(../img/4sec/car_bg.png) center (center/cover) no-repeat;
  &_img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto;
    z-index: 2; }
  .container {
    position: relative;
    min-height: 702px; }
  .label {
    color:#000 {}
    background-color: $color_white; }
  &_descr {
    text-align: center;
    color: #222222;
    font-size: 17px;
    font-weight: 300;
    line-height: 24px; } }

/* 5 sec */

.require {
  padding: 68px 0 89px;
  &_block {
    margin: 31px 0;
    &_nbm {
      margin-bottom: 0; } }
  &_title {
    font-weight: 400;
    font-size: 22px;
    color: #1CA8C3;
    margin-bottom: 22px; }
  &_list {
    list-style-type: none;
    padding-left: 50;
    li {
      position: relative;
      font-weight: 300;
      font-size: 17px;
      color: #222222;
      padding-bottom: 0;
      margin-bottom: 10px;
      line-height: 24px;
      &:before {
        content: '';
        display: block;
        position: absolute;
        left: -32px;
        top: 2px;
        height: 21px;
        width: 20px;
        background: url(../img/5sec/check_blue.svg) center (center/contain) no-repeat; } } }
  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #1CA8C3; }
  .warning {
    .require_title {
      color: $color_pink; }
    .require_list {
      li {
        &:before {
          background: url(../img/5sec/check_red.svg) center (center/contain) no-repeat; } } } } }

/* 6 sec */

.world {
  position: relative;
  min-height: 720px;
  padding: 51px 0 22px;
  background: url(../img/6sec/world_bg.jpg) center (center/cover) no-repeat;
  .label {
    background-color: #fff;
    color: #000; }
  &_cities {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    bottom: 49px;
    text-align: center;
    font-weight: 400;
    font-size: 15px;
    text-transform: uppercase;
    color: #1FBAD6;
    &:hover {
      text-decoration: none;
      color: #fff; } } }

/* footer */

footer {
  padding: 11px 0 40px;
  a {
    &:hover {
      text-decoration: none; } } }
.footer {
  &_line {
    margin: 0 auto;
    margin-bottom: 33px;
    width: 299px;
    height: 4px;
    background: #1FBAD6; }
  &_wrapper {
    padding-left: 21px;
    padding-right: 21px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  &_wrapper > div {
    width: 33%;
    &:nth-child(3) {
      display: flex;
      justify-content: flex-end; } }
  &_social {
    display: flex;
    &_item {
      display: -webkit-flex;
      display: -moz-flex;
      display: -ms-flex;
      display: -o-flex;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 100%;
      background-color: #000;
      height: 35px;
      width: 35px;
      margin-right: 6px;
      i {
        color: #fff; } } }
  &_links {
    width: 100%;
    max-width: 420px;
    margin: 0 auto;
    &_main {
      display: flex;
      justify-content: space-between;
      align-items: center;
      a {
        font-weight: 300;
        font-size: 15px;
        text-transform: uppercase;
        color: #070716;
        margin-bottom: 13px; } }

    &_sub {
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      a {
        font-weight: 300;
        font-size: 11px;
        text-transform: uppercase;
        color: #999999;
        margin-bottom: 17px; } }
    &_lang {
      display: block;
      margin: 0 auto;
      font-weight: 400;
      font-size: 11px;
      text-transform: uppercase;
      color: #1FBAD6;
      text-align: center; } } }
.xs-vsible {
  display: none; }
