@media (max-width: 1200px) {
  .menu {
    &_item {
      padding-right: 20px; }
    &_link {
      font-size: 13px; } }
  .promo {
    &_header {}
    font-size: 50px; }
  .advantages {
    &_item {
      align-items: flex-start; }
    &_desc {
      width: 300px; } } }


@media (max-width: 991px) {
  .menu {
    &_item {
      padding-right: 8px; }
    &_link {
      font-size: 10px; } }
  .subheader {
    img {
      width: 100%; }
    &_official {
      font-size: 11px; }
    &_tel {
      font-size: 18px; }
    &_btn {
      width: 100%;
      margin-top: 21px;
      height: 40px; } }
  .promo {
    &_header {
      font-size: 40px; }
    &_subheader {
      font-size: 30px; } }
  .advantages {
    &_item {
      flex-direction: column;
      align-items: center; }
    &_circle {
      margin-right: 0; }
    &_desc {
      margin-top: 20px;
      width: 300px; } }
  .footer {
      &_wrapper {
        flex-direction: column;
        align-items: center; }
      &_wrapper > div {
        width: 100%;
        margin-top: 30px;
        &:nth-child(3) {
          justify-content: center; } }
      &_social {
        justify-content: center; } }
  .opportunities {
    &_img {
      width: 100%; } }

  @media (max-width: 767px) {
    .menu {
      position: fixed;
      top: 0;
      left: -100%;
      width: 50%;
      height: 100vh;
      padding: 20px 15px;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: rgba(0,0,0,0.8);
      transition: 0.6s all;
      &_active {
        left: 0; }
      &_item {
        padding-right: 0;
        border: 0;
        margin-top: 10px; }
      &_link {
        font-size: 20px; } }
    .promo {
      &_header {
        line-height: 45px; } }
    .hamburger {
      display: block;
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      height: 12px;
      width: 26px;
      span {
        display: block;
        background-color: #fff;
        height: 2px;
        width: 100%;
        margin-bottom: 2px;
        transition: 0.5s all; }
      &_active {
        span {
          margin-bottom: -2px;
          &:nth-child(1) {
            transform: translateY(3px) rotate(-45deg); }
          &:nth-child(2) {
            display: none; }
          &:nth-child(3) {
            transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
      display: none; }
    .xs-vsible {
      display: block; }
    .subheader {
      &_tel {
        font-weight: 400;
        font-size: 16px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        display: block;
        margin-top: 10px; }
      &_btn {
        width: 200px;
        height: 40px;
        margin: 0 auto;
        display: block;
        margin-top: 10px; } }
    .opportunities {
      background: url(../img/4sec/dark_bg.png) center (center/cover) no-repeat;
      min-height: 430px;
      padding: 43px 0 45px;
      .container {
        min-height: 430px; }
      &_img {
        margin-top: 45px; }
      &_descr {
        margin-top: 23px;
        font-size: 17px;
        line-height: 24px;
        color: #FFFFFF; } } }

  @media (max-width: 575px) {
    nav {
      height: 30px; }
    header {
      height: 30px; }
    .menu {
      width: 85%;
      &_item {
        width: 100%;
        text-align: center;
        padding: 5px 0; }
      &_link {
        display: block; } }


    .subheader {
      top: 30px;
      height: 62px;
      &_official {
        display: none; }
      &_logo {
        margin-top: 16px; }
      &_tel {
        font-size: 12px;
        text-transform: uppercase;
        margin-top: 11px; }
      &_btn {
        width: 110px;
        height: 22px;
        font-weight: normal;
        font-size: 10px;
        margin-top: 6px;
        &:hover {
          background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%); } } }
    .promo {
      min-height: 360px;
      padding: 94px 0 35px 0;
      &_descr {
        display: none; }
      &_header {
        font-weight: 900;
        font-size: 30px;
        line-height: 35px;
        padding: 0 24px; }
      &_subheader {
        font-weight: 900;
        font-size: 20px;
        line-height: 23px;
        font-weight: bold;
        padding: 0 10px; }
      &_btn {
        margin-top: 72px;
        width: 210px;
        height: 50px;
        font-size: 16px;
        &:hover {
          background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%); } } }
    .advantages {
      padding: 22px 0 38px;
      &_item {
        margin-top: 26px; }
      &_circle {
        height: 100px;
        width: 100px; }
      &_desc {
        width: 100%;
        margin-top: 27px; }
      &_header {
        font-size: 16px; } }
    .title {
      font-size: 16px;
      margin-top: 15px;
      line-height: 16px; }
    .mobile {
      padding: 25px 0 43px;
      .subtitle {
        margin-top: 9px;
        font-size: 14px; }
      .row {
        margin-top: 27px; }
      &_item {
        width: 290px;
        height: 254px;
        margin-bottom: 30px;
        padding-top: 80px;
        &_subtitle {
          font-size: 18px;
          line-height: 31px; }
        &_plus {
          bottom: 21px; } } }
    .opportunities {
      background: url(../img/4sec/dark_bg.png) center (center/cover) no-repeat;
      min-height: 393px;
      padding-top: 25px;
      padding-bottom: 31px;
      .container {
        min-height: 279px;
        .title,
        .subtitle {
          display: none; } }
      &_img {
        margin-top: 34px;
        left: -15px; }
      &_descr {
        margin-top: 23px; } }
    .require {
      padding-top: 22px;
      padding-bottom: 0;
      .title {
        margin-bottom: 31px; }
      &_title {
        font-size: 16px;
        margin-bottom: 5px; }
      &_block {
        margin-top: 0; }
      &_list {
        list-style-type: none;
        padding-left: 31px;
        margin-bottom: 15px;
        li {
          font-size: 14px;
          line-height: 16px;
          margin-bottom: 5px;
          &:before {
            top: -3px;
            left: -31px; } } } }
    .world {
      padding-top: 26px;
      padding-bottom: 0;
      min-height: 360px;
      .label {
        font-size: 11px;
        height: 22px;
        width: 75px; }
      .subtitle {
        margin-top: 11px;
        font-size: 14px;
        line-height: 21px; }
      &_cities {
        width: 100%;
        font-size: 15px;
        bottom: 29px; } }

    .footer {
      padding-top: 24px;
      padding-bottom: 29px;
      &_line {
        width: 255px;
        margin-bottom: 21px; }
      &_wrapper {
        padding-left: 18px;
        padding-right: 18px; }
      &_links_main {
        font-size: 14px;
        a {
          margin-bottom: 10px; } }
      &_links_sub {
        flex-wrap: wrap;
        padding-left: 20px;
        padding-right: 20px;
        a {
          margin-bottom: 12px; } }
      &_wrapper > div {
        margin-top: 0;
        &:nth-child(2) {
          order: 0;
          margin-bottom: 16px; }
        &:nth-child(1) {
          order: 1;
          margin-bottom: 19px; }
        &:nth-child(3) {
          order: 2; } }
      &_apps {
        padding-left: 14px;
        padding-right: 14px;
        img {
          width: 100%; } } } } }
